import React, { Component } from 'react';

import '../App.css';


class Buttons extends Component {
  constructor() {
    super();
    this.state = { 
         up:"",
         down:"",
         right:"",
         left:"",
         speed:""
       }
  }
  


   render() {
 
const upStart = () =>{
    this.setState({up: "True"});
    
  }

  const upStop = () =>{
      this.setState({up: "False"});
    }

  const downStart = () =>{
      this.setState({down: "True"});
    }

  const downStop = () =>{
      this.setState({down: "False"});
    }

  const leftStart = () =>{
      this.setState({left: "True"});
    }

  const leftStop = () =>{
      this.setState({left: "False"});
    }

  const rightStart = () =>{
      this.setState({right: "True"});
    }

  const rightStop = () =>{
     this.setState({right: "False"});
    }

  const myFunction1 = (event) =>{
    var x = event.key;
    if (x === "ArrowUp"){
      this.setState({up: "True"});
    }
    if (x === "ArrowDown"){
      this.setState({down: "True"});
    }
    if (x === "ArrowRight"){
      this.setState({right: "True"});
    }
    if (x === "ArrowLeft"){
      this.setState({left: "True"});
    }
  }

  const myFunction2 = (event) =>{
    var x = event.key;
    if (x === "ArrowUp"){
      this.setState({up: "False"});
    }
    if (x === "ArrowDown"){
      this.setState({down: "False"});
    }
    if (x === "ArrowRight"){
      this.setState({right: "False"});
    }
    if (x === "ArrowLeft"){
      this.setState({left: "False"});
    }
  }
  
  const gearFunc = (event) =>{
      let e = document.getElementById("gears");
      let result = e.options[e.selectedIndex].value;
      this.setState({speed: result});
  }

  fetch('https://stormy-ravine-51277.herokuapp.com/values',{
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        up : this.state.up,
        down : this.state.down,
        left : this.state.left,
        right : this.state.right,
        speed : this.state.speed
      })
  })
       return (
          <div>            
             <div className="App Mrgn-gears">
              <form >
                <label  for="gears">Choose speed level:</label>
                <select name="gears" id="gears" onChange={gearFunc}>
                  <option value="1">1st</option>
                  <option value="2">2nd</option>
                  <option value="3">3rd</option>
                  <option value="4">4th</option>
                </select>
              </form>
             </div>

             <div className="App">
                <input  type="text" placeholder="For Arrow Controls Press Here" size="22" onKeyDown={myFunction1} onKeyUp={myFunction2}/>  
             </div>     
             <div className="Common">
                 <div className="btn ">
                    <p><button className="Mrgn-btn PNP" type="button">upPNP</button></p>
                    <p><button className="Mrgn-btn PNP" type="button">downPNP</button></p>
                 </div>
           
                 <div className="App  Arrows">
                    <div >
                      <button onMouseDown={upStart} onMouseUp={upStop} onTouchStart={upStart} onTouchEnd={upStop} className="Mrgn-btn" type="button">up</button>
                    </div>
                    <div>
                      <button onMouseDown={leftStart} onMouseUp={leftStop} onTouchStart={leftStart} onTouchEnd={leftStop} className="Mrgn-btn" type="button">left</button>
                      <button onMouseDown={rightStart} onMouseUp={rightStop} onTouchStart={rightStart} onTouchEnd={rightStop} className="Mrgn-btn" type="button">right</button>
                    </div>
                    <div>
                       <button onMouseDown={downStart} onMouseUp={downStop} onTouchStart={downStart} onTouchEnd={downStop} className="Mrgn-btn" type="button">down</button>
                    </div>
                 </div>

                 <div className="btn ">
                    <p> <button className="Mrgn-btn PNP" type="button">upCam</button></p>
                    <p><button className="Mrgn-btn PNP" type="button">downCam</button></p>
                 </div>
              </div>
          </div>                            
      );
  }
  }

export default Buttons;
